.shop-basic-box {
    :hover {
        transform: scale(1.1);
        -webkit-transform: scale(1.1);
        // -moz-transform: scale(1.1);
    }
    width: 23%;
    min-width: 170px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

    h6 {
        text-align: center;
        width: 80%;
        color: #193171;
    }
}

@media (min-width: 1200px) {
    .shop-basic-box h6 {
        font-size: 1.2rem;

        .price-part span {
            font-size: 1.2rem;
        }
    }
}
